.section-products {
  background-color: $color-main;
  padding-top: 122px;
  padding-bottom: 98px;

  text-align: center;

  @media screen and (min-width: $tablet) {
    padding-bottom: 112px;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 126px;
    padding-bottom: 120px;
  }

  .product-benefit {
    margin-bottom: 15px;

    font-family: Titan One;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;

    text-transform: uppercase;

    color: $color-background-hero;

    @media screen and (min-width: $tablet) {
      font-size: 26px;
      line-height: 1.15;
    }

    @media screen and (min-width: $desktop) {
      font-size: 30px;
      line-height: 1.13;
      letter-spacing: 0.04em;
    }
  }

  .product-title {
    margin-bottom: 144px;

    font-family: Titan One;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.147;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: $color-accent;

    @media screen and (min-width: $tablet) {
      margin-bottom: 150px;
      font-size: 48px;
      line-height: 1.145;
    }

    @media screen and (min-width: $desktop) {
      margin-bottom: 260px;
      font-size: 58px;
      line-height: 1.13;
      letter-spacing: 0.06em;
    }
  }
}

.product-list {
  @media screen and (min-width: $tablet) {
    display: flex;
    justify-content: space-between;

    @media screen and (min-width: $desktop) {
      justify-content: center;
    }
  }
}

.product-item {
  position: relative;
  padding: 160px 40px 40px;

  color: $color-main;
  background-color: $color-main-product-1;
  border-radius: 24px;

  &:not(:last-child) {
    @media screen and (max-width: 739px) {
      margin-bottom: 130px;
    }

    @media screen and (min-width: $desktop) {
      margin-right: 30px;
    }
  }

  @media screen and (min-width: $tablet) {
    width: 220px;
    padding: 152px 11px 40px;
  }

  @media screen and (min-width: $desktop) {
    width: 329px;
    height: 525px;
    padding: 163px 44px 50px;
  }

  .product-item-title {
    position: relative;
    padding-bottom: 30px;
    font-family: Titan One;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.5;
    letter-spacing: 0.04em;

    &::after {
      content: '';
      display: block;
      width: 26px;
      height: 6px;

      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%);

      background-image: url(./../images/vector/dots.svg);
      background-repeat: no-repeat;
      background-size: contain;
    }

    @media screen and (min-width: $desktop) {
      padding-bottom: 32px;
      font-size: 30px;
      line-height: 1.13;
    }
  }

  .product-item-text {
    padding-top: 30px;
    padding-bottom: 30px;
    font-family: DM Sans;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    letter-spacing: 0.04em;

    color: $color-main;

    @media screen and (min-width: $tablet) {
      padding-bottom: 40px;
      line-height: 1.78;
    }

    @media screen and (min-width: $desktop) {
      padding-bottom: 62px;
      font-size: 16px;
      line-height: 1.75;
    }
  }

  .product-link {
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 50%;

    color: $color-accent;
  }

  .product-img {
    position: absolute;
    top: -113px;
    left: 16px;

    @media screen and (min-width: $tablet) {
      width: 220px;
      top: -21%;
      left: 0%;
    }

    @media screen and (min-width: $desktop) {
      width: 335px;
      top: -40%;
    }
  }

  &.beige-bg {
    background-color: $color-main-product-2;
  }

  &.green-bg {
    background-color: $color-main-product-3;
  }
}
