.hero {
  background-color: $color-main-product-1;
  background-repeat: no-repeat;

  @media screen and (max-width: 739px) {
    padding-top: 137px;
    padding-bottom: 160px;

    background-image: url('./../images/bitmap/hero/ice-cream/mob@1x.png'),
      url('./../images/vector/ellipse.svg');
    background-size: 171px 410px, 346px 344px;
    background-position: left calc(50% + 40px) bottom,
      left calc(50% + 45px) top 21px;
  }

  @media screen and (min-width: $tablet) and (max-width: 1279px) {
    padding-top: 102px;

    background-image: url('./../images/bitmap/hero/girl/tab@1x.png'),
      url('./../images/bitmap/hero/ice-cream/tab@1x.png'),
      url('./../images/vector/ellipse.svg');
    background-size: 168px 133px, 143px 336px, 324px 322px;
    background-position: left calc(50% + 265px) bottom,
      left calc(50% + 65px) bottom, left calc(50% + 85px) top 35px;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 171px;

    background-image: url('./../images/bitmap/hero/girl/desk@1x.png'),
      url('./../images/bitmap/hero/ice-cream/desk@1x.png'),
      url('./../images/vector/ellipse.svg');
    background-size: 293px 232px, 236px 557px, 538px 538px;
    background-position: left calc(50% + 420px) bottom,
      left calc(50% + 100px) bottom, left calc(50% + 130px) top 50px;
  }
}

@media screen and (min-device-pixel-ratio: 2),
  screen and (-webkit-min-device-pixel-ratio: 2),
  screen and (-o-min-device-pixel-ratio: 2/1),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  .hero {
    background-image: url('./../images/bitmap/hero/ice-cream/mob@2x.png'),
      url('./../images/vector/ellipse.svg');

    @media screen and (min-width: $tablet) {
      background-image: url('./../images/bitmap/hero/girl/tab@2x.png'),
        url('./../images/bitmap/hero/ice-cream/tab@2x.png'),
        url('./../images/vector/ellipse.svg');
    }

    @media screen and (min-width: $desktop) {
      background-image: url('./../images/bitmap/hero/girl/desk@2x.png'),
        url('./../images/bitmap/hero/ice-cream/desk@2x.png'),
        url('./../images/vector/ellipse.svg');
    }
  }
}

.hero .container {
  @media screen and (min-width: $tablet) {
    padding-bottom: 28px;
  }

  @media screen and (min-width: $desktop) {
    padding-bottom: 45px;
  }
}

.upper-line {
  display: flex;
  justify-content: space-between;

  @media screen and (min-width: $tablet) {
    padding-right: 7px;
  }

  @media screen and (min-width: $desktop) {
    margin-bottom: 60px;
    padding-left: 70px;
    padding-right: 70px;
  }
}

.hero-title {
  max-width: 225px;
  margin-bottom: 20px;

  font-family: 'Titan One', sans-serif;
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $color-main;

  @media screen and (min-width: $tablet) {
    max-width: 180px;
    font-size: 22px;
  }

  @media screen and (min-width: $desktop) {
    max-width: 277px;
    font-size: 24px;
  }

  .text {
    font-size: 42px;
    color: $color-accent;

    @media screen and (min-width: 260px) {
      font-size: 48px;
    }

    @media screen and (min-width: $tablet) {
      font-size: 37px;
    }

    @media screen and (min-width: $desktop) {
      font-size: 58px;
    }
  }
}

.anchors {
  display: flex;
}

.anchors-item:not(:last-child) {
  margin-right: 8px;

  @media screen and (min-width: $desktop) {
    margin-right: 15px;
  }
}

.anchors-button {
  display: block;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 9px;
  padding-bottom: 9px;
  border-radius: 20px;

  font-family: 'DM Sans', sans-serif;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25;

  background-color: $color-main;
  color: $color-accent;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: $color-accent-button-2;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 23px;
    padding-right: 23px;

    font-size: 14px;
    line-height: 1.3;
  }
}

.anchors-button-red {
  background-color: $color-accent;
  color: $color-main;

  &:hover,
  &:focus {
    background-color: $color-accent-button-1;
  }
}

.places {
  @media screen and (max-width: 739px) {
    display: none;
  }
}

.places-item {
  position: relative;

  &::before {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(40%, -30%);
    z-index: 0;
    content: '';
    display: block;

    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: $color-main;

    @media screen and (min-width: $desktop) {
      width: 30px;
      height: 30px;
    }
  }
}

.places-item:not(:last-child) {
  margin-bottom: 17px;

  @media screen and (min-width: $desktop) {
    margin-bottom: 20px;
  }
}

.places-box {
  text-align: right;
}

.places-number {
  position: relative;
  z-index: 5;

  font-family: 'Titan One', sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $color-accent;

  @media screen and (min-width: $desktop) {
    font-size: 46px;
    line-height: 1.13;
  }
}

.places-text {
  font-family: 'DM Sans', sans-serif;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25;

  color: $color-main;

  @media screen and (min-width: $desktop) {
    font-size: 14px;
    line-height: 1.3;
  }
}

.eco {
  display: flex;
  align-items: flex-end;

  @media screen and (max-width: 739px) {
    display: none;
  }
}

.eco-content {
  max-width: 115px;
  padding-bottom: 6px;

  @media screen and (min-width: $desktop) {
    max-width: 200px;
    padding-bottom: 11px;
  }
}

.eco-img {
  display: none;

  @media screen and (min-width: $tablet) {
    display: inline-block;
  }

  @media screen and (min-width: $desktop) {
    margin-right: 30px;
  }
}

.eco-text {
  margin-bottom: 7px;

  font-family: 'DM Sans', sans-serif;

  font-weight: 400;
  font-size: 8px;
  line-height: 1.5;

  color: $color-main;

  @media screen and (min-width: $desktop) {
    margin-bottom: 20px;

    font-size: 14px;
    line-height: 1.4;
  }
}

.eco-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  background-color: $color-main;
  border-radius: 50%;

  @media screen and (min-width: $desktop) {
    width: 26px;
    height: 26px;
  }
}

.arrow-right {
  fill: $color-accent;
}
